.loadingContener {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 3;
  justify-content: center;
  align-items: center;
  background-color: rgba(0,0,0,0.4);
  .loadingContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 15%;
    background-color: white;
    width: 50%;
    p {
      text-align: center;
      line-height: 5rem;
      font-weight: 700;
    }
  }

}



.starWars {
  padding: 55px 0;
  display: flex;
  justify-content: center;
  img {
    transform: scale(1.2);
  }
  svg {
    display: none;
  }
}
div.mainMenuDiv {
  display: block;
  margin: 0 0 20px 0;
  nav {
    background-color: var(--bgc);
    padding: 10px 0px;
    ul {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      width: fit-content;
      margin-left: auto;
      margin-right: auto;
      li {
        display: block;
        padding: 0 10px;
        &:not(:first-child) {
          border-left: 1px dotted white;
        }
        a {
          display: block;
          cursor: pointer;
          padding: 5px 1px;
          text-decoration: none;
          text-transform: uppercase;
          span {
            color: var(--menuFont);
            font-size: 1.1rem;
            margin-left: 5px;
          }
          svg > path{
            fill: var(--menuFont);
          }
          svg {
            font-size: 1.1rem;
          }
          &.active {
            span {
              color: var(--menuFontActive);
              text-decoration: underline;
              font-weight: 600;
            }
          }
          &:hover span {
            color: var(--menuFontActive); 
          }
        }
      }
    }
  }
}
div#mySidebar {
  display: none;
}
.start {
  display: flex;
  justify-content: center;
  margin-top: 50px;
  .board {
    background-color: var(--bgc);
    padding: 10px 20px;
    width: 90%;
    p {
      color: var(--menuFont);
      margin: 10px 0;
    }
  }
}
.contentWrap {
  display: flex;
}
.left {
  float: none;
  display: flex;
  flex-direction: column;
  flex: 30%;
  margin-left: 20px;
}
.right {
  float: none;
  display: flex;
  width: auto;
  flex: 70%;
  padding: 0 150px;
}
input.search {
  font-size: 1rem;
  width: 70%;
  margin: 0 3px;
  padding: 3px 5px;
}
.selectedList {
  overflow: auto;
  padding: 0px 10px; 
  margin: 5px 3px;
  background-color: var(--bgc);
  max-height: 60vh;
  a {
    display: block;
    padding: 5px 10px;
    font-size: 1rem;
    text-decoration: none;
    color: lightgrey;
    &:hover {
        background-color: grey;
        transition-duration: 0.3s;
        font-weight: 600;
    }
    &.active {
        font-weight: 600;
        background-color: grey;
    }
  }
  li {
    list-style-type: none;
    cursor: pointer;
    padding: 0;
    transition-property: background-color;
  }
}
.detailsSection {
  padding: 10px;
  background-color: var(--bgc);
  color: #DCE7F0;
  width: 100%;
  margin: 5px 3px;
  overflow: auto;
  box-sizing: border-box;
  div {
      width: 100%;
  }
  p.detailsTitle {
      color: white;
      text-transform: uppercase;
      font-size: 1.2rem;
  }
  .detailsWrap {
    position: relative; 
    display: flex;
    margin: 10px 0;   
    flex-basis: 100%;
    &:not(:first-of-type) {
      border-top: 1px solid grey;
    }
    .detailsName {
      flex-basis: 40%;
      font-size: 1rem;
      color: #DCE7F0;
    }
    .detailsProp {
      color: #DCE7F0;
      font-size: 1rem;
      flex-basis: 60%;
      a {
        color: #DCE7F0;
        font-size: 1rem;
      }
      li:hover {
        background-color: #5AA2EB;
        transition-duration: 0.3s;
        font-weight: 600;
      }
      a:hover {
        background-color: #5AA2EB;
        transition-duration: 0.3s;
        font-weight: 600;
      }
    }
  }
}

